import React from 'react';
import { Card, Row, Col, Statistic, Typography, Space } from 'antd';
import {
  DatabaseOutlined,
  CheckSquareOutlined,
  BarChartOutlined,
  MobileOutlined,
  ThunderboltOutlined,
  CloudUploadOutlined,
  MonitorOutlined,
  SettingOutlined,
  FileTextOutlined,
  ArrowUpOutlined,
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '@/store/hooks';

const { Title, Paragraph } = Typography;

interface MenuCard {
  key: string;
  title: string;
  description: string;
  icon: React.ReactNode;
  path: string;
  color: string;
  external?: boolean;
}

const HomePage: React.FC = () => {
  const navigate = useNavigate();
  const { filteredItems: cards } = useAppSelector(state => state.extendedCards);

  const menuCards: MenuCard[] = [
    {
      key: 'registry',
      title: 'Реестры',
      description: 'Управление реестром залогового имущества',
      icon: <DatabaseOutlined style={{ fontSize: 48 }} />,
      path: '/registry',
      color: '#1890ff',
    },
    {
      key: 'tasks',
      title: 'Задачи',
      description: 'Управление задачами и процессами',
      icon: <CheckSquareOutlined style={{ fontSize: 48 }} />,
      path: 'https://jfsagro-glitch.github.io/zadachnik/',
      color: '#52c41a',
      external: true,
    },
    {
      key: 'reports',
      title: 'Отчеты',
      description: 'Аналитика и отчетность',
      icon: <BarChartOutlined style={{ fontSize: 48 }} />,
      path: '/reports',
      color: '#722ed1',
    },
    {
      key: 'mobile-appraiser',
      title: 'Мобильный оценщик',
      description: 'Мобильное приложение для оценки',
      icon: <MobileOutlined style={{ fontSize: 48 }} />,
      path: '/mobile-appraiser',
      color: '#fa8c16',
    },
    {
      key: 'smartdeal',
      title: 'SmartDeal',
      description: 'Умные сделки и автоматизация',
      icon: <ThunderboltOutlined style={{ fontSize: 48 }} />,
      path: '/smartdeal',
      color: '#eb2f96',
    },
    {
      key: 'upload',
      title: 'Загрузка',
      description: 'Загрузка документов и файлов',
      icon: <CloudUploadOutlined style={{ fontSize: 48 }} />,
      path: '/upload',
      color: '#13c2c2',
    },
    {
      key: 'monitoring',
      title: 'Мониторинг',
      description: 'Мониторинг системы и процессов',
      icon: <MonitorOutlined style={{ fontSize: 48 }} />,
      path: '/monitoring',
      color: '#faad14',
    },
    {
      key: 'settings',
      title: 'Настройки',
      description: 'Настройки системы и пользователей',
      icon: <SettingOutlined style={{ fontSize: 48 }} />,
      path: '/settings',
      color: '#8c8c8c',
    },
  ];

  // Статистика
  const approvedCards = cards.filter(card => card.status === 'approved').length;
  const editingCards = cards.filter(card => card.status === 'editing').length;
  const totalDocuments = cards.reduce((sum, card) => sum + card.documents.length, 0);

  return (
    <div>
      <Space direction="vertical" size="large" style={{ width: '100%' }}>
        {/* Заголовок */}
        <div>
          <Title level={2}>
            <DatabaseOutlined /> Добро пожаловать в CMS
          </Title>
          <Paragraph type="secondary">
            Система управления залоговым имуществом
          </Paragraph>
        </div>

        {/* Статистика */}
        <Row gutter={[16, 16]}>
          <Col xs={24} sm={8}>
            <Card>
              <Statistic
                title="Всего карточек"
                value={cards.length}
                prefix={<FileTextOutlined />}
                valueStyle={{ color: '#1890ff' }}
              />
            </Card>
          </Col>
          <Col xs={24} sm={8}>
            <Card>
              <Statistic
                title="Утверждено"
                value={approvedCards}
                prefix={<ArrowUpOutlined />}
                valueStyle={{ color: '#52c41a' }}
                suffix={`/ ${cards.length}`}
              />
            </Card>
          </Col>
          <Col xs={24} sm={8}>
            <Card>
              <Statistic
                title="Документов"
                value={totalDocuments}
                prefix={<FileTextOutlined />}
                valueStyle={{ color: '#722ed1' }}
              />
            </Card>
          </Col>
        </Row>

        {/* Меню функций */}
        <div>
          <Title level={3}>Функции системы</Title>
          <Row gutter={[16, 16]}>
            {menuCards.map(card => (
              <Col xs={24} sm={12} md={8} lg={6} key={card.key}>
                <Card
                  hoverable
                  onClick={() => {
                    if (card.external) {
                      window.open(card.path, '_blank');
                    } else {
                      navigate(card.path);
                    }
                  }}
                  style={{
                    borderLeft: `4px solid ${card.color}`,
                    height: '100%',
                    cursor: 'pointer',
                  }}
                  bodyStyle={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    textAlign: 'center',
                    padding: '24px 16px',
                  }}
                >
                  <div style={{ color: card.color, marginBottom: 16 }}>
                    {card.icon}
                  </div>
                  <Title level={4} style={{ marginBottom: 8 }}>
                    {card.title}
                  </Title>
                  <Paragraph type="secondary" style={{ marginBottom: 0, fontSize: 13 }}>
                    {card.description}
                  </Paragraph>
                </Card>
              </Col>
            ))}
          </Row>
        </div>

        {/* Быстрые действия */}
        <div>
          <Title level={3}>Быстрый доступ</Title>
          <Row gutter={[16, 16]}>
            <Col xs={24} md={8}>
              <Card
                hoverable
                onClick={() => navigate('/registry')}
                style={{ borderTop: '3px solid #1890ff' }}
              >
                <Statistic
                  title="Перейти к реестру"
                  value={cards.length}
                  prefix={<DatabaseOutlined />}
                  suffix="карточек"
                  valueStyle={{ color: '#1890ff', fontSize: 24 }}
                />
              </Card>
            </Col>
            <Col xs={24} md={8}>
              <Card
                hoverable
                onClick={() => window.open('https://jfsagro-glitch.github.io/zadachnik/', '_blank')}
                style={{ borderTop: '3px solid #52c41a' }}
              >
                <Statistic
                  title="Активные задачи"
                  value={editingCards}
                  prefix={<CheckSquareOutlined />}
                  suffix="в работе"
                  valueStyle={{ color: '#52c41a', fontSize: 24 }}
                />
              </Card>
            </Col>
            <Col xs={24} md={8}>
              <Card
                hoverable
                onClick={() => navigate('/reports')}
                style={{ borderTop: '3px solid #722ed1' }}
              >
                <Statistic
                  title="Отчеты"
                  value={0}
                  prefix={<BarChartOutlined />}
                  suffix="доступно"
                  valueStyle={{ color: '#722ed1', fontSize: 24 }}
                />
              </Card>
            </Col>
          </Row>
        </div>
      </Space>
    </div>
  );
};

export default HomePage;

