import React, { useState, useEffect } from 'react';
import { Button, Space, Modal, message, Breadcrumb, Input, Card, Row, Col, Statistic } from 'antd';
import {
  PlusOutlined,
  ExportOutlined,
  ImportOutlined,
  CloudDownloadOutlined,
  SearchOutlined,
  ArrowLeftOutlined,
  FileTextOutlined,
  CheckCircleOutlined,
  EditOutlined,
  RobotOutlined,
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import CollateralCardForm from '@/components/common/CollateralCardForm';
import CollateralCardView from '@/components/common/CollateralCardView';
import AIAssistant from '@/components/common/AIAssistant';
import RegistryTable from '@/components/common/RegistryTable';
import type { RegistryTableRecord } from '@/components/common/RegistryTable';
import { useAppDispatch, useAppSelector } from '@/store/hooks';
import deepSeekService from '@/services/DeepSeekService';
import {
  setExtendedCards,
  addExtendedCard,
  updateExtendedCard,
  deleteExtendedCard,
  deleteExtendedCards,
  setExtendedLoading,
} from '@/store/slices/extendedCardsSlice';
import extendedStorageService from '@/services/ExtendedStorageService';
import { downloadFile } from '@/utils/helpers';
import type { ExtendedCollateralCard } from '@/types';

const ExtendedRegistryPage: React.FC = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const { filteredItems: cards, loading } = useAppSelector((state: any) => state.extendedCards);
  const [modalVisible, setModalVisible] = useState(false);
  const [viewModalVisible, setViewModalVisible] = useState(false);
  const [editingCard, setEditingCard] = useState<ExtendedCollateralCard | null>(null);
  const [viewingCard, setViewingCard] = useState<ExtendedCollateralCard | null>(null);
  const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);
  const [searchText, setSearchText] = useState('');
  const [aiAssistantVisible, setAiAssistantVisible] = useState(false);
  const [selectedCardForAI, setSelectedCardForAI] = useState<ExtendedCollateralCard | null>(null);

  const loadCards = async () => {
    try {
      dispatch(setExtendedLoading(true));
      const loadedCards = await extendedStorageService.getExtendedCards();
      dispatch(setExtendedCards(loadedCards));
    } catch (error) {
      message.error('Ошибка загрузки данных');
      console.error(error);
    } finally {
      dispatch(setExtendedLoading(false));
    }
  };

  // Загрузка данных при монтировании
  useEffect(() => {
    loadCards();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleCreate = () => {
    setEditingCard(null);
    setModalVisible(true);
  };

  const handleEdit = (card: ExtendedCollateralCard) => {
    setEditingCard(card);
    setModalVisible(true);
  };

  const handleDelete = async (id: string) => {
    try {
      await extendedStorageService.deleteExtendedCard(id);
      dispatch(deleteExtendedCard(id));
      message.success('Карточка удалена');
    } catch (error) {
      message.error('Ошибка удаления карточки');
      console.error(error);
    }
  };

  const handleDeleteMultiple = async () => {
    try {
      await extendedStorageService.deleteExtendedCards(selectedRowKeys as string[]);
      dispatch(deleteExtendedCards(selectedRowKeys as string[]));
      message.success(`Удалено карточек: ${selectedRowKeys.length}`);
      setSelectedRowKeys([]);
    } catch (error) {
      message.error('Ошибка удаления карточек');
      console.error(error);
    }
  };

  const handleSubmit = async (values: ExtendedCollateralCard) => {
    try {
      await extendedStorageService.saveExtendedCard(values);

      if (editingCard) {
        dispatch(updateExtendedCard(values));
        message.success('Карточка обновлена');
      } else {
        dispatch(addExtendedCard(values));
        message.success('Карточка создана');
      }

      setModalVisible(false);
      setEditingCard(null);
    } catch (error) {
      message.error('Ошибка сохранения карточки');
      console.error(error);
    }
  };

  const handleExport = async () => {
    try {
      const result = await extendedStorageService.exportToExcel(
        cards,
        `registry_export_${new Date().toISOString().split('T')[0]}`
      );
      if (result.success) {
        message.success(result.message);
      } else {
        message.error(result.message);
      }
    } catch (error) {
      message.error('Ошибка экспорта данных');
      console.error(error);
    }
  };

  const handleExportBackup = async () => {
    try {
      const blob = await extendedStorageService.exportBackup();
      const filename = `cms_backup_${new Date().toISOString().split('T')[0]}.json`;
      downloadFile(blob, filename);
      message.success('Резервная копия создана');
    } catch (error) {
      message.error('Ошибка создания резервной копии');
      console.error(error);
    }
  };

  const handleImportBackup = async (file: File) => {
    try {
      await extendedStorageService.importBackup(file);
      message.success('Резервная копия восстановлена');
      await loadCards();
    } catch (error) {
      message.error('Ошибка восстановления резервной копии');
      console.error(error);
    }
  };

  const handleOpenAIAssistant = (card?: ExtendedCollateralCard) => {
    setSelectedCardForAI(card || null);
    setAiAssistantVisible(true);
  };

  const handleApplyAIDescription = (description: string) => {
    if (selectedCardForAI) {
      // Обновляем карточку с новым описанием
      const updatedCard = {
        ...selectedCardForAI,
        description,
        updatedAt: new Date(),
      };
      handleSubmit(updatedCard);
    }
  };

  const hasAIEnabled = deepSeekService.hasApiKey();

  // Конвертируем данные для RegistryTable
  const tableData: RegistryTableRecord[] = cards.map((card: any) => ({
    id: card.id,
    number: card.number,
    name: card.name,
    mainCategory: card.mainCategory,
    status: card.status,
    classification: card.classification,
    addresses: card.address ? [{ fullAddress: card.address.fullAddress }] : [],
    createdAt: card.createdAt,
    updatedAt: card.updatedAt,
  }));

  const handleView = (id: string) => {
    const card = cards.find((c: any) => c.id === id);
    if (card) {
      setViewingCard(card);
      setViewModalVisible(true);
    }
  };

  const handleDoubleClick = (record: RegistryTableRecord) => {
    const card = cards.find((c: any) => c.id === record.id);
    if (card) {
      setViewingCard(card);
      setViewModalVisible(true);
    }
  };

  // Статистика для карточек
  const approvedCards = cards.filter((card: any) => card.status === 'approved').length;
  const editingCards = cards.filter((card: any) => card.status === 'editing').length;
  const totalDocuments = cards.reduce((sum: any, card: any) => sum + card.documents.length, 0);

  return (
    <div>
      <Breadcrumb style={{ marginBottom: 16 }}>
        <Breadcrumb.Item>
          <a onClick={() => navigate('/')}>Главная</a>
        </Breadcrumb.Item>
        <Breadcrumb.Item>Реестры</Breadcrumb.Item>
      </Breadcrumb>

      {/* Кнопка "Назад" и статистика */}
      <Card style={{ marginBottom: 16 }}>
        <Row gutter={[16, 16]} align="middle">
          <Col>
            <Button 
              icon={<ArrowLeftOutlined />} 
              onClick={() => navigate('/')}
              size="large"
            >
              Назад в меню
            </Button>
          </Col>
          <Col flex="auto">
            <Row gutter={[16, 16]}>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="Всего карточек"
                  value={cards.length}
                  prefix={<FileTextOutlined />}
                  valueStyle={{ color: '#1890ff', fontSize: 20 }}
                />
              </Col>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="Утверждено"
                  value={approvedCards}
                  prefix={<CheckCircleOutlined />}
                  valueStyle={{ color: '#52c41a', fontSize: 20 }}
                />
              </Col>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="На редактировании"
                  value={editingCards}
                  prefix={<EditOutlined />}
                  valueStyle={{ color: '#faad14', fontSize: 20 }}
                />
              </Col>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="Документов"
                  value={totalDocuments}
                  prefix={<FileTextOutlined />}
                  valueStyle={{ color: '#722ed1', fontSize: 20 }}
                />
              </Col>
            </Row>
          </Col>
        </Row>
      </Card>

      <Space style={{ marginBottom: 16 }} wrap>
        <Button type="primary" icon={<PlusOutlined />} onClick={handleCreate}>
          Создать карточку
        </Button>
        {hasAIEnabled && (
          <Button 
            icon={<RobotOutlined />} 
            onClick={() => handleOpenAIAssistant()}
            style={{ borderColor: '#1890ff', color: '#1890ff' }}
          >
            AI-Ассистент
          </Button>
        )}
        <Button icon={<ExportOutlined />} onClick={handleExport}>
          Экспорт в Excel
        </Button>
        <Button icon={<CloudDownloadOutlined />} onClick={handleExportBackup}>
          Создать резервную копию
        </Button>
        <input
          type="file"
          accept=".json"
          style={{ display: 'none' }}
          id="backup-import"
          onChange={(e: any) => {
            const file = e.target.files?.[0];
            if (file) handleImportBackup(file);
          }}
        />
        <Button
          icon={<ImportOutlined />}
          onClick={() => document.getElementById('backup-import')?.click()}
        >
          Восстановить из копии
        </Button>

        {selectedRowKeys.length > 0 && (
          <Button danger onClick={handleDeleteMultiple}>
            Удалить выбранные ({selectedRowKeys.length})
          </Button>
        )}

        <Input
          placeholder="Поиск по названию или номеру..."
          prefix={<SearchOutlined />}
          value={searchText}
          onChange={(e: any) => setSearchText(e.target.value)}
          style={{ width: 300 }}
          allowClear
        />
      </Space>

      <RegistryTable
        data={tableData}
        loading={loading}
        onEdit={(id: any) => handleEdit(cards.find((c: any) => c.id === id)!)}
        onView={handleView}
        onDelete={handleDelete}
        onDoubleClick={handleDoubleClick}
      />

      {/* Модальное окно просмотра карточки */}
      <Modal
        title={viewingCard ? `${viewingCard.number} - ${viewingCard.name}` : 'Просмотр карточки'}
        open={viewModalVisible}
        onCancel={() => {
          setViewModalVisible(false);
          setViewingCard(null);
        }}
        footer={[
          <Button
            key="edit"
            type="primary"
            onClick={() => {
              if (viewingCard) {
                setViewModalVisible(false);
                handleEdit(viewingCard);
              }
            }}
          >
            Редактировать
          </Button>,
          <Button
            key="close"
            onClick={() => {
              setViewModalVisible(false);
              setViewingCard(null);
            }}
          >
            Закрыть
          </Button>,
        ]}
        width="90%"
        style={{ top: 20 }}
        styles={{ 
          body: { 
            maxHeight: 'calc(100vh - 200px)', 
            overflowY: 'auto',
            paddingTop: 16,
          } 
        }}
        destroyOnClose
      >
        {viewingCard && <CollateralCardView card={viewingCard} />}
      </Modal>

      {/* Модальное окно редактирования карточки */}
      <Modal
        title={editingCard ? 'Редактирование карточки' : 'Создание карточки'}
        open={modalVisible}
        onCancel={() => {
          setModalVisible(false);
          setEditingCard(null);
        }}
        footer={null}
        width="90%"
        style={{ top: 20 }}
        styles={{ 
          body: { 
            maxHeight: 'calc(100vh - 200px)', 
            overflowY: 'auto',
            paddingTop: 16,
          } 
        }}
        destroyOnClose
      >
        <CollateralCardForm
          initialValues={editingCard || undefined}
          onSubmit={handleSubmit}
          onCancel={() => {
            setModalVisible(false);
            setEditingCard(null);
          }}
        />
      </Modal>

      <AIAssistant
        visible={aiAssistantVisible}
        onClose={() => {
          setAiAssistantVisible(false);
          setSelectedCardForAI(null);
        }}
        card={selectedCardForAI || undefined}
        onApplyDescription={handleApplyAIDescription}
      />
    </div>
  );
};

export default ExtendedRegistryPage;

