import React from 'react';
import { Layout, Menu } from 'antd';
import {
  DatabaseOutlined,
  CheckSquareOutlined,
  BarChartOutlined,
  MobileOutlined,
  ThunderboltOutlined,
  CloudUploadOutlined,
  MonitorOutlined,
  SettingOutlined,
} from '@ant-design/icons';
import { useNavigate, useLocation } from 'react-router-dom';
import type { MenuItem } from '@/types';

const { Sider } = Layout;

interface SidebarMenuProps {
  collapsed: boolean;
}

const SidebarMenu: React.FC<SidebarMenuProps> = ({ collapsed }) => {
  const navigate = useNavigate();
  const location = useLocation();

  // Определяем структуру меню
  const menuItems: MenuItem[] = [
    {
      key: 'registry',
      label: 'Реестры',
      icon: <DatabaseOutlined />,
      path: '/registry',
    },
    {
      key: 'tasks',
      label: 'Задачи',
      icon: <CheckSquareOutlined />,
      path: 'https://jfsagro-glitch.github.io/zadachnik/',
      external: true,
    },
    {
      key: 'reports',
      label: 'Отчеты',
      icon: <BarChartOutlined />,
      path: '/reports',
    },
    {
      key: 'mobile-appraiser',
      label: 'Мобильный оценщик',
      icon: <MobileOutlined />,
      path: '/mobile-appraiser',
    },
    {
      key: 'smartdeal',
      label: 'SmartDeal',
      icon: <ThunderboltOutlined />,
      path: '/smartdeal',
    },
    {
      key: 'upload',
      label: 'Загрузка',
      icon: <CloudUploadOutlined />,
      path: '/upload',
    },
    {
      key: 'monitoring',
      label: 'Мониторинг',
      icon: <MonitorOutlined />,
      path: '/monitoring',
    },
    {
      key: 'settings',
      label: 'Настройки',
      icon: <SettingOutlined />,
      path: '/settings',
    },
  ];

  // Преобразуем MenuItem[] в формат, нужный для Ant Design Menu
  const antdMenuItems = menuItems.map(item => ({
    key: item.key,
    icon: item.icon,
    label: item.label,
    onClick: () => {
      if (item.path) {
        if (item.external) {
          // Открываем внешнюю ссылку в новой вкладке
          window.open(item.path, '_blank');
        } else {
          // Навигация внутри приложения
          navigate(item.path);
        }
      }
    },
  }));

  // Определяем активный пункт меню на основе текущего пути
  const getSelectedKey = (): string => {
    const pathParts = location.pathname.split('/').filter(Boolean);
    if (pathParts.length === 0) return 'registry'; // По умолчанию
    return pathParts[0];
  };

  return (
    <Sider
      collapsible
      collapsed={collapsed}
      trigger={null}
      width={250}
      style={{
        overflow: 'auto',
        height: '100vh',
        position: 'fixed',
        left: 0,
        top: 0,
        bottom: 0,
      }}
    >
      <div
        style={{
          height: '64px',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          color: '#fff',
          fontSize: collapsed ? '16px' : '20px',
          fontWeight: 'bold',
          transition: 'all 0.2s',
          gap: '8px',
        }}
      >
        <DatabaseOutlined style={{ fontSize: collapsed ? '20px' : '28px' }} />
        {!collapsed && <span>CMS</span>}
      </div>
      <Menu
        theme="dark"
        mode="inline"
        selectedKeys={[getSelectedKey()]}
        items={antdMenuItems}
      />
    </Sider>
  );
};

export default SidebarMenu;

