import React, { useState } from 'react';
import {
  Modal,
  Button,
  Space,
  Typography,
  Card,
  Spin,
  message,
  Alert,
  Tabs,
  Input,
} from 'antd';
import {
  RobotOutlined,
  BulbOutlined,
  DollarOutlined,
  FileTextOutlined,
  CheckCircleOutlined,
} from '@ant-design/icons';
import deepSeekService from '@/services/DeepSeekService';
import type { ExtendedCollateralCard } from '@/types';

const { Title, Paragraph, Text } = Typography;
const { TextArea } = Input;
const { TabPane } = Tabs;

interface AIAssistantProps {
  visible: boolean;
  onClose: () => void;
  card?: ExtendedCollateralCard;
  onApplyDescription?: (description: string) => void;
}

const AIAssistant: React.FC<AIAssistantProps> = ({
  visible,
  onClose,
  card,
  onApplyDescription,
}) => {
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<string>('');
  const [activeTab, setActiveTab] = useState('description');

  const hasApiKey = deepSeekService.hasApiKey();

  const handleGenerateDescription = async () => {
    if (!card) return;

    setLoading(true);
    setResult('');
    try {
      const description = await deepSeekService.generatePropertyDescription({
        type: card.classification.level1,
        address: card.address?.fullAddress,
        area: card.characteristics?.area as number,
        characteristics: card.characteristics,
      });
      setResult(description);
      message.success('Описание сгенерировано');
    } catch (error: any) {
      message.error(error.message || 'Ошибка генерации описания');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleGenerateValuation = async () => {
    if (!card) return;

    setLoading(true);
    setResult('');
    try {
      const valuation = await deepSeekService.suggestMarketValue({
        type: card.classification.level1,
        address: card.address?.fullAddress,
        area: card.characteristics?.area as number,
        characteristics: card.characteristics,
      });
      setResult(valuation);
      message.success('Оценка сгенерирована');
    } catch (error: any) {
      message.error(error.message || 'Ошибка генерации оценки');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleAnalyzeDocuments = async () => {
    if (!card || card.documents.length === 0) {
      message.warning('Нет документов для анализа');
      return;
    }

    setLoading(true);
    setResult('');
    try {
      const analyses = await Promise.all(
        card.documents.slice(0, 3).map(doc =>
          deepSeekService.analyzeDocument(doc.name, doc.type)
        )
      );
      setResult(analyses.join('\n\n---\n\n'));
      message.success('Документы проанализированы');
    } catch (error: any) {
      message.error(error.message || 'Ошибка анализа документов');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleGetRecommendations = async () => {
    if (!card) return;

    setLoading(true);
    setResult('');
    try {
      const recommendations = await deepSeekService.getRecommendations({
        type: card.classification.level1,
        address: card.address,
        characteristics: card.characteristics,
        documents: card.documents.length,
        partners: card.partners.length,
      });
      setResult(recommendations);
      message.success('Рекомендации сгенерированы');
    } catch (error: any) {
      message.error(error.message || 'Ошибка генерации рекомендаций');
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  const handleApply = () => {
    if (result && onApplyDescription) {
      onApplyDescription(result);
      message.success('Описание применено');
      onClose();
    }
  };

  return (
    <Modal
      title={
        <Space>
          <RobotOutlined style={{ fontSize: 24, color: '#1890ff' }} />
          <Title level={4} style={{ margin: 0 }}>
            AI-Ассистент
          </Title>
        </Space>
      }
      open={visible}
      onCancel={onClose}
      width={800}
      footer={[
        <Button key="close" onClick={onClose}>
          Закрыть
        </Button>,
        activeTab === 'description' && result && (
          <Button
            key="apply"
            type="primary"
            icon={<CheckCircleOutlined />}
            onClick={handleApply}
          >
            Применить описание
          </Button>
        ),
      ]}
    >
      {!hasApiKey ? (
        <Alert
          message="API ключ не настроен"
          description={
            <Space direction="vertical">
              <Text>
                Для использования AI-ассистента необходимо добавить API ключ DeepSeek в настройках.
              </Text>
              <Button type="primary" href="/#/settings">
                Перейти в настройки
              </Button>
            </Space>
          }
          type="warning"
          showIcon
        />
      ) : (
        <Tabs activeKey={activeTab} onChange={setActiveTab}>
          <TabPane
            tab={
              <Space>
                <FileTextOutlined />
                Описание объекта
              </Space>
            }
            key="description"
          >
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              <Card size="small">
                <Paragraph>
                  AI создаст профессиональное описание объекта на основе его характеристик и адреса.
                </Paragraph>
                <Button
                  type="primary"
                  icon={<BulbOutlined />}
                  onClick={handleGenerateDescription}
                  loading={loading}
                  disabled={!card}
                >
                  Сгенерировать описание
                </Button>
              </Card>

              {loading && (
                <Card>
                  <div style={{ textAlign: 'center', padding: '20px' }}>
                    <Spin size="large" />
                    <Paragraph style={{ marginTop: 16 }}>
                      AI анализирует данные объекта...
                    </Paragraph>
                  </div>
                </Card>
              )}

              {result && !loading && (
                <Card title="Сгенерированное описание">
                  <TextArea
                    value={result}
                    onChange={(e) => setResult(e.target.value)}
                    rows={8}
                    style={{ marginBottom: 16 }}
                  />
                  <Alert
                    message="Вы можете отредактировать текст перед применением"
                    type="info"
                    showIcon
                  />
                </Card>
              )}
            </Space>
          </TabPane>

          <TabPane
            tab={
              <Space>
                <DollarOutlined />
                Оценка стоимости
              </Space>
            }
            key="valuation"
          >
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              <Card size="small">
                <Paragraph>
                  AI предложит предварительную оценку рыночной стоимости объекта с учетом его характеристик.
                </Paragraph>
                <Button
                  type="primary"
                  icon={<DollarOutlined />}
                  onClick={handleGenerateValuation}
                  loading={loading}
                  disabled={!card}
                >
                  Сгенерировать оценку
                </Button>
              </Card>

              {loading && (
                <Card>
                  <div style={{ textAlign: 'center', padding: '20px' }}>
                    <Spin size="large" />
                    <Paragraph style={{ marginTop: 16 }}>
                      AI анализирует рыночные данные...
                    </Paragraph>
                  </div>
                </Card>
              )}

              {result && !loading && (
                <Card title="Предварительная оценка">
                  <Paragraph style={{ whiteSpace: 'pre-wrap' }}>{result}</Paragraph>
                  <Alert
                    message="Это предварительная оценка. Для точной оценки требуется профессиональная экспертиза."
                    type="warning"
                    showIcon
                  />
                </Card>
              )}
            </Space>
          </TabPane>

          <TabPane
            tab={
              <Space>
                <FileTextOutlined />
                Анализ документов
              </Space>
            }
            key="documents"
          >
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              <Card size="small">
                <Paragraph>
                  AI проанализирует документы и даст рекомендации по их проверке.
                </Paragraph>
                <Button
                  type="primary"
                  icon={<FileTextOutlined />}
                  onClick={handleAnalyzeDocuments}
                  loading={loading}
                  disabled={!card || card.documents.length === 0}
                >
                  Анализировать документы
                </Button>
              </Card>

              {loading && (
                <Card>
                  <div style={{ textAlign: 'center', padding: '20px' }}>
                    <Spin size="large" />
                    <Paragraph style={{ marginTop: 16 }}>
                      AI анализирует документы...
                    </Paragraph>
                  </div>
                </Card>
              )}

              {result && !loading && (
                <Card title="Анализ документов">
                  <Paragraph style={{ whiteSpace: 'pre-wrap' }}>{result}</Paragraph>
                </Card>
              )}
            </Space>
          </TabPane>

          <TabPane
            tab={
              <Space>
                <BulbOutlined />
                Рекомендации
              </Space>
            }
            key="recommendations"
          >
            <Space direction="vertical" size="middle" style={{ width: '100%' }}>
              <Card size="small">
                <Paragraph>
                  AI проанализирует карточку объекта и даст рекомендации по улучшению полноты данных.
                </Paragraph>
                <Button
                  type="primary"
                  icon={<BulbOutlined />}
                  onClick={handleGetRecommendations}
                  loading={loading}
                  disabled={!card}
                >
                  Получить рекомендации
                </Button>
              </Card>

              {loading && (
                <Card>
                  <div style={{ textAlign: 'center', padding: '20px' }}>
                    <Spin size="large" />
                    <Paragraph style={{ marginTop: 16 }}>
                      AI анализирует карточку...
                    </Paragraph>
                  </div>
                </Card>
              )}

              {result && !loading && (
                <Card title="Рекомендации">
                  <Paragraph style={{ whiteSpace: 'pre-wrap' }}>{result}</Paragraph>
                </Card>
              )}
            </Space>
          </TabPane>
        </Tabs>
      )}
    </Modal>
  );
};

export default AIAssistant;

