import React, { useState, useEffect } from 'react';
import { 
  Card, 
  Row, 
  Col, 
  Button, 
  Space, 
  Breadcrumb, 
  Statistic,
  List,
  Tag,
  Progress,
  Input,
  Select
} from 'antd';
import { 
  PlusOutlined, 
  SearchOutlined, 
  ArrowLeftOutlined,
  FileTextOutlined,
  CheckCircleOutlined,
  ClockCircleOutlined,
  UserOutlined
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

const { Search } = Input;
const { Option } = Select;

interface Task {
  id: string;
  title: string;
  description: string;
  status: 'todo' | 'in_progress' | 'completed' | 'review';
  priority: 'low' | 'medium' | 'high' | 'critical';
  assignee: string;
  dueDate: string;
  createdAt: string;
  project: string;
}

const TasksPage: React.FC = () => {
  const navigate = useNavigate();
  const [tasks] = useState<Task[]>([
    {
      id: '1',
      title: 'Проверка документов по объекту №1',
      description: 'Необходимо проверить комплектность документов',
      status: 'in_progress',
      priority: 'high',
      assignee: 'Иванов И.И.',
      dueDate: '2024-10-15',
      createdAt: '2024-10-01',
      project: 'Реестр залогового имущества'
    },
    {
      id: '2',
      title: 'Согласование оценки',
      description: 'Согласовать рыночную стоимость объекта',
      status: 'review',
      priority: 'medium',
      assignee: 'Петров П.П.',
      dueDate: '2024-10-20',
      createdAt: '2024-10-02',
      project: 'Оценка объектов'
    },
    {
      id: '3',
      title: 'Обновление характеристик',
      description: 'Добавить новые характеристики объекта',
      status: 'todo',
      priority: 'low',
      assignee: 'Сидоров С.С.',
      dueDate: '2024-10-25',
      createdAt: '2024-10-03',
      project: 'Реестр залогового имущества'
    }
  ]);

  const [filteredTasks, setFilteredTasks] = useState<Task[]>(tasks);
  const [searchText, setSearchText] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [priorityFilter, setPriorityFilter] = useState<string>('all');

  // Фильтрация задач
  useEffect(() => {
    let result = tasks;
    
    if (searchText) {
      result = result.filter(task => 
        task.title.toLowerCase().includes(searchText.toLowerCase()) ||
        task.description.toLowerCase().includes(searchText.toLowerCase())
      );
    }
    
    if (statusFilter !== 'all') {
      result = result.filter(task => task.status === statusFilter);
    }
    
    if (priorityFilter !== 'all') {
      result = result.filter(task => task.priority === priorityFilter);
    }
    
    setFilteredTasks(result);
  }, [tasks, searchText, statusFilter, priorityFilter]);

  const getStatusColor = (status: string) => {
    const colors = {
      todo: 'default',
      in_progress: 'processing',
      completed: 'success',
      review: 'warning'
    };
    return colors[status as keyof typeof colors] || 'default';
  };

  const getStatusText = (status: string) => {
    const texts = {
      todo: 'К выполнению',
      in_progress: 'В работе',
      completed: 'Завершено',
      review: 'На согласовании'
    };
    return texts[status as keyof typeof texts] || status;
  };

  const getPriorityColor = (priority: string) => {
    const colors = {
      low: 'green',
      medium: 'blue',
      high: 'orange',
      critical: 'red'
    };
    return colors[priority as keyof typeof colors] || 'default';
  };

  const getPriorityText = (priority: string) => {
    const texts = {
      low: 'Низкий',
      medium: 'Средний',
      high: 'Высокий',
      critical: 'Критический'
    };
    return texts[priority as keyof typeof texts] || priority;
  };

  // Статистика
  const totalTasks = tasks.length;
  const completedTasks = tasks.filter(task => task.status === 'completed').length;
  const inProgressTasks = tasks.filter(task => task.status === 'in_progress').length;
  const completionRate = totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0;

  return (
    <div>
      <Breadcrumb style={{ marginBottom: 16 }}>
        <Breadcrumb.Item>
          <a onClick={() => navigate('/')}>Главная</a>
        </Breadcrumb.Item>
        <Breadcrumb.Item>Задачи</Breadcrumb.Item>
      </Breadcrumb>

      {/* Кнопка "Назад" и статистика */}
      <Card style={{ marginBottom: 16 }}>
        <Row gutter={[16, 16]} align="middle">
          <Col>
            <Button 
              icon={<ArrowLeftOutlined />} 
              onClick={() => navigate('/')}
              size="large"
            >
              Назад в меню
            </Button>
          </Col>
          <Col flex="auto">
            <Row gutter={[16, 16]}>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="Всего задач"
                  value={totalTasks}
                  prefix={<FileTextOutlined />}
                  valueStyle={{ color: '#1890ff', fontSize: 20 }}
                />
              </Col>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="В работе"
                  value={inProgressTasks}
                  prefix={<ClockCircleOutlined />}
                  valueStyle={{ color: '#faad14', fontSize: 20 }}
                />
              </Col>
              <Col xs={24} sm={8} md={6}>
                <Statistic
                  title="Завершено"
                  value={completedTasks}
                  prefix={<CheckCircleOutlined />}
                  valueStyle={{ color: '#52c41a', fontSize: 20 }}
                />
              </Col>
              <Col xs={24} sm={8} md={6}>
                <div>
                  <div style={{ marginBottom: 8, color: '#666' }}>Прогресс выполнения</div>
                  <Progress percent={completionRate} size="small" />
                </div>
              </Col>
            </Row>
          </Col>
        </Row>
      </Card>

      {/* Панель фильтров и поиска */}
      <Card style={{ marginBottom: 16 }}>
        <Row gutter={[16, 16]} align="middle">
          <Col xs={24} sm={12} md={8}>
            <Search
              placeholder="Поиск по задачам..."
              prefix={<SearchOutlined />}
              value={searchText}
              onChange={e => setSearchText(e.target.value)}
              allowClear
            />
          </Col>
          <Col xs={12} sm={6} md={4}>
            <Select
              value={statusFilter}
              onChange={setStatusFilter}
              style={{ width: '100%' }}
              placeholder="Статус"
            >
              <Option value="all">Все статусы</Option>
              <Option value="todo">К выполнению</Option>
              <Option value="in_progress">В работе</Option>
              <Option value="review">На согласовании</Option>
              <Option value="completed">Завершено</Option>
            </Select>
          </Col>
          <Col xs={12} sm={6} md={4}>
            <Select
              value={priorityFilter}
              onChange={setPriorityFilter}
              style={{ width: '100%' }}
              placeholder="Приоритет"
            >
              <Option value="all">Все приоритеты</Option>
              <Option value="low">Низкий</Option>
              <Option value="medium">Средний</Option>
              <Option value="high">Высокий</Option>
              <Option value="critical">Критический</Option>
            </Select>
          </Col>
          <Col xs={24} sm={12} md={8} style={{ textAlign: 'right' }}>
            <Space>
              <Button type="primary" icon={<PlusOutlined />}>
                Создать задачу
              </Button>
            </Space>
          </Col>
        </Row>
      </Card>

      {/* Список задач */}
      <Card title="Список задач">
        <List
          dataSource={filteredTasks}
          renderItem={task => (
            <List.Item
              actions={[
                <Button key="edit" type="link">Изменить</Button>,
                <Button key="complete" type="link">Завершить</Button>
              ]}
            >
              <List.Item.Meta
                avatar={<UserOutlined style={{ fontSize: 24 }} />}
                title={
                  <div>
                    <span style={{ marginRight: 8 }}>{task.title}</span>
                    <Tag color={getStatusColor(task.status)}>
                      {getStatusText(task.status)}
                    </Tag>
                    <Tag color={getPriorityColor(task.priority)}>
                      {getPriorityText(task.priority)}
                    </Tag>
                  </div>
                }
                description={
                  <div>
                    <div>{task.description}</div>
                    <div style={{ marginTop: 8, fontSize: '12px', color: '#666' }}>
                      <span>Исполнитель: {task.assignee}</span>
                      <span style={{ marginLeft: 16 }}>Срок: {task.dueDate}</span>
                      <span style={{ marginLeft: 16 }}>Проект: {task.project}</span>
                    </div>
                  </div>
                }
              />
            </List.Item>
          )}
        />
      </Card>
    </div>
  );
};

export default TasksPage;