/**
 * DeepSeek AI Service
 * Сервис для работы с DeepSeek API
 */

export interface DeepSeekMessage {
  role: 'system' | 'user' | 'assistant';
  content: string;
}

export interface DeepSeekRequest {
  model: string;
  messages: DeepSeekMessage[];
  temperature?: number;
  max_tokens?: number;
  stream?: boolean;
}

export interface DeepSeekResponse {
  id: string;
  object: string;
  created: number;
  model: string;
  choices: Array<{
    index: number;
    message: {
      role: string;
      content: string;
    };
    finish_reason: string;
  }>;
  usage: {
    prompt_tokens: number;
    completion_tokens: number;
    total_tokens: number;
  };
}

export interface AIAssistantOptions {
  systemPrompt?: string;
  temperature?: number;
  maxTokens?: number;
}

class DeepSeekService {
  private apiKey: string = '';
  private baseURL: string = 'https://api.deepseek.com/v1';
  private defaultModel: string = 'deepseek-chat';

  /**
   * Установить API ключ
   */
  setApiKey(apiKey: string): void {
    this.apiKey = apiKey;
  }

  /**
   * Получить текущий API ключ
   */
  getApiKey(): string {
    return this.apiKey;
  }

  /**
   * Проверить, установлен ли API ключ
   */
  hasApiKey(): boolean {
    return !!this.apiKey && this.apiKey.length > 0;
  }

  /**
   * Проверить валидность API ключа
   */
  async validateApiKey(apiKey?: string): Promise<boolean> {
    const keyToValidate = apiKey || this.apiKey;
    if (!keyToValidate) return false;

    try {
      const response = await fetch(`${this.baseURL}/chat/completions`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${keyToValidate}`,
        },
        body: JSON.stringify({
          model: this.defaultModel,
          messages: [{ role: 'user', content: 'test' }],
          max_tokens: 1,
        }),
      });

      return response.ok;
    } catch (error) {
      console.error('API key validation failed:', error);
      return false;
    }
  }

  /**
   * Отправить запрос к DeepSeek API
   */
  async chat(
    messages: DeepSeekMessage[],
    options?: AIAssistantOptions
  ): Promise<DeepSeekResponse> {
    if (!this.hasApiKey()) {
      throw new Error('API ключ DeepSeek не установлен. Пожалуйста, добавьте ключ в настройках.');
    }

    const request: DeepSeekRequest = {
      model: this.defaultModel,
      messages,
      temperature: options?.temperature ?? 0.7,
      max_tokens: options?.maxTokens ?? 2000,
      stream: false,
    };

    try {
      const response = await fetch(`${this.baseURL}/chat/completions`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${this.apiKey}`,
        },
        body: JSON.stringify(request),
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(
          errorData.error?.message || `HTTP error! status: ${response.status}`
        );
      }

      const data: DeepSeekResponse = await response.json();
      return data;
    } catch (error) {
      console.error('DeepSeek API error:', error);
      throw error;
    }
  }

  /**
   * Простой запрос к AI (одно сообщение)
   */
  async ask(
    question: string,
    options?: AIAssistantOptions
  ): Promise<string> {
    const messages: DeepSeekMessage[] = [];

    // Добавляем системный промпт, если указан
    if (options?.systemPrompt) {
      messages.push({
        role: 'system',
        content: options.systemPrompt,
      });
    }

    messages.push({
      role: 'user',
      content: question,
    });

    const response = await this.chat(messages, options);
    return response.choices[0]?.message?.content || '';
  }

  /**
   * AI-ассистент для описания объекта недвижимости
   */
  async generatePropertyDescription(data: {
    type: string;
    address?: string;
    area?: number;
    characteristics?: Record<string, any>;
  }): Promise<string> {
    const systemPrompt = `Ты профессиональный оценщик недвижимости. 
Создай краткое, информативное описание объекта недвижимости на основе предоставленных данных.
Описание должно быть профессиональным, структурированным и содержать все ключевые характеристики.`;

    const userPrompt = `Создай описание для объекта:
Тип: ${data.type}
${data.address ? `Адрес: ${data.address}` : ''}
${data.area ? `Площадь: ${data.area} кв.м` : ''}
${data.characteristics ? `Характеристики: ${JSON.stringify(data.characteristics, null, 2)}` : ''}

Создай краткое профессиональное описание (3-5 предложений).`;

    return await this.ask(userPrompt, { systemPrompt, temperature: 0.7 });
  }

  /**
   * AI-ассистент для генерации рыночной оценки
   */
  async suggestMarketValue(data: {
    type: string;
    address?: string;
    area?: number;
    characteristics?: Record<string, any>;
  }): Promise<string> {
    const systemPrompt = `Ты эксперт по оценке недвижимости в России.
На основе предоставленных данных дай предварительную оценку рыночной стоимости объекта.
Укажи диапазон цен и факторы, влияющие на стоимость.`;

    const userPrompt = `Оцени объект:
Тип: ${data.type}
${data.address ? `Адрес: ${data.address}` : ''}
${data.area ? `Площадь: ${data.area} кв.м` : ''}
${data.characteristics ? `Характеристики: ${JSON.stringify(data.characteristics, null, 2)}` : ''}

Предоставь предварительную оценку стоимости и факторы, влияющие на цену.`;

    return await this.ask(userPrompt, { systemPrompt, temperature: 0.5 });
  }

  /**
   * AI-ассистент для анализа документов
   */
  async analyzeDocument(documentName: string, documentType: string): Promise<string> {
    const systemPrompt = `Ты юридический консультант по недвижимости.
Проанализируй тип документа и дай рекомендации по его проверке.`;

    const userPrompt = `Проанализируй документ:
Название: ${documentName}
Тип: ${documentType}

Какие ключевые моменты нужно проверить в этом документе?`;

    return await this.ask(userPrompt, { systemPrompt, temperature: 0.5 });
  }

  /**
   * AI-ассистент для генерации рекомендаций
   */
  async getRecommendations(cardData: any): Promise<string> {
    const systemPrompt = `Ты эксперт по залоговому имуществу.
Проанализируй карточку объекта и дай рекомендации по улучшению полноты данных.`;

    const userPrompt = `Проанализируй карточку объекта:
${JSON.stringify(cardData, null, 2)}

Какие данные можно добавить или улучшить для более полной информации об объекте?`;

    return await this.ask(userPrompt, { systemPrompt, temperature: 0.6 });
  }
}

// Создаем единственный экземпляр сервиса
const deepSeekService = new DeepSeekService();

export default deepSeekService;

