import React, { useState, useEffect } from 'react';
import {
  Card,
  Form,
  Input,
  Button,
  Space,
  message,
  Tabs,
  Typography,
  Row,
  Col,
  Breadcrumb,
  Divider,
  Alert,
  Tag,
} from 'antd';
import {
  ArrowLeftOutlined,
  KeyOutlined,
  CheckCircleOutlined,
  CloseCircleOutlined,
  SaveOutlined,
  EyeInvisibleOutlined,
  EyeOutlined,
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '@/store/hooks';
import { setSettings } from '@/store/slices/appSlice';
import extendedStorageService from '@/services/ExtendedStorageService';
import deepSeekService from '@/services/DeepSeekService';
import type { ApiKeys } from '@/types';

const { Title, Paragraph, Text } = Typography;
const { TabPane } = Tabs;

const SettingsPage: React.FC = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const { theme, apiKeys } = useAppSelector(state => state.app);
  
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [validating, setValidating] = useState(false);
  const [apiKeyStatus, setApiKeyStatus] = useState<{
    deepseek?: boolean;
    openai?: boolean;
    anthropic?: boolean;
  }>({});

  useEffect(() => {
    // Инициализация формы с текущими настройками
    form.setFieldsValue({
      deepseekApiKey: apiKeys?.deepseek || '',
      openaiApiKey: apiKeys?.openai || '',
      anthropicApiKey: apiKeys?.anthropic || '',
    });

    // Инициализация DeepSeek сервиса
    if (apiKeys?.deepseek) {
      deepSeekService.setApiKey(apiKeys.deepseek);
    }
  }, [apiKeys, form]);

  const handleValidateDeepSeek = async () => {
    const apiKey = form.getFieldValue('deepseekApiKey');
    if (!apiKey) {
      message.warning('Введите API ключ для проверки');
      return;
    }

    setValidating(true);
    try {
      const isValid = await deepSeekService.validateApiKey(apiKey);
      setApiKeyStatus(prev => ({ ...prev, deepseek: isValid }));
      
      if (isValid) {
        message.success('API ключ DeepSeek валиден');
      } else {
        message.error('API ключ DeepSeek невалиден');
      }
    } catch (error) {
      message.error('Ошибка проверки API ключа');
      setApiKeyStatus(prev => ({ ...prev, deepseek: false }));
    } finally {
      setValidating(false);
    }
  };

  const handleSave = async () => {
    try {
      const values = await form.validateFields();
      setLoading(true);

      const newApiKeys: ApiKeys = {
        deepseek: values.deepseekApiKey || undefined,
        openai: values.openaiApiKey || undefined,
        anthropic: values.anthropicApiKey || undefined,
      };

      // Сохраняем настройки
      await extendedStorageService.saveSettings({
        apiKeys: newApiKeys,
      });

      // Обновляем Redux store
      dispatch(setSettings({ apiKeys: newApiKeys }));

      // Устанавливаем API ключ в сервис
      if (newApiKeys.deepseek) {
        deepSeekService.setApiKey(newApiKeys.deepseek);
      }

      message.success('Настройки сохранены');
    } catch (error) {
      console.error('Failed to save settings:', error);
      message.error('Ошибка сохранения настроек');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <Breadcrumb style={{ marginBottom: 16 }}>
        <Breadcrumb.Item>
          <a onClick={() => navigate('/')}>Главная</a>
        </Breadcrumb.Item>
        <Breadcrumb.Item>Настройки</Breadcrumb.Item>
      </Breadcrumb>

      {/* Кнопка "Назад" */}
      <Card style={{ marginBottom: 16 }}>
        <Button 
          icon={<ArrowLeftOutlined />} 
          onClick={() => navigate('/')}
          size="large"
        >
          Назад в меню
        </Button>
      </Card>

      <Card>
        <Title level={2}>
          <KeyOutlined /> Настройки системы
        </Title>
        
        <Tabs defaultActiveKey="api">
          <TabPane tab="API ключи" key="api">
            <Space direction="vertical" size="large" style={{ width: '100%' }}>
              <Alert
                message="Настройка AI-ассистентов"
                description="Добавьте API ключи для использования AI-ассистентов в системе. AI может помогать с описанием объектов, оценкой стоимости и анализом документов."
                type="info"
                showIcon
              />

              <Form
                form={form}
                layout="vertical"
                onFinish={handleSave}
              >
                {/* DeepSeek API */}
                <Card 
                  title={
                    <Space>
                      <Text strong>DeepSeek API</Text>
                      {apiKeyStatus.deepseek !== undefined && (
                        apiKeyStatus.deepseek ? (
                          <Tag color="success" icon={<CheckCircleOutlined />}>
                            Валиден
                          </Tag>
                        ) : (
                          <Tag color="error" icon={<CloseCircleOutlined />}>
                            Невалиден
                          </Tag>
                        )
                      )}
                    </Space>
                  }
                  style={{ marginBottom: 16 }}
                >
                  <Paragraph type="secondary">
                    DeepSeek - мощная AI модель для анализа и генерации текста.
                    Используется для создания описаний объектов и рекомендаций.
                  </Paragraph>
                  
                  <Form.Item
                    name="deepseekApiKey"
                    label="API ключ DeepSeek"
                    rules={[
                      { 
                        pattern: /^sk-[a-z0-9]{32}$/,
                        message: 'Неверный формат API ключа (должен начинаться с sk-)'
                      }
                    ]}
                  >
                    <Input.Password
                      placeholder="sk-xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
                      prefix={<KeyOutlined />}
                      iconRender={(visible) => 
                        visible ? <EyeOutlined /> : <EyeInvisibleOutlined />
                      }
                      visibilityToggle
                    />
                  </Form.Item>

                  <Space>
                    <Button
                      onClick={handleValidateDeepSeek}
                      loading={validating}
                    >
                      Проверить ключ
                    </Button>
                    <Button
                      type="link"
                      href="https://platform.deepseek.com/api_keys"
                      target="_blank"
                    >
                      Получить API ключ
                    </Button>
                  </Space>
                </Card>

                {/* OpenAI API (для будущего использования) */}
                <Card 
                  title={<Text strong>OpenAI API</Text>}
                  style={{ marginBottom: 16 }}
                >
                  <Paragraph type="secondary">
                    OpenAI API (ChatGPT) - для расширенных возможностей AI.
                    <Tag color="orange" style={{ marginLeft: 8 }}>Скоро</Tag>
                  </Paragraph>
                  
                  <Form.Item
                    name="openaiApiKey"
                    label="API ключ OpenAI"
                  >
                    <Input.Password
                      placeholder="sk-..."
                      prefix={<KeyOutlined />}
                      disabled
                    />
                  </Form.Item>
                </Card>

                {/* Anthropic API (для будущего использования) */}
                <Card 
                  title={<Text strong>Anthropic API (Claude)</Text>}
                  style={{ marginBottom: 16 }}
                >
                  <Paragraph type="secondary">
                    Anthropic Claude - альтернативная AI модель.
                    <Tag color="orange" style={{ marginLeft: 8 }}>Скоро</Tag>
                  </Paragraph>
                  
                  <Form.Item
                    name="anthropicApiKey"
                    label="API ключ Anthropic"
                  >
                    <Input.Password
                      placeholder="sk-ant-..."
                      prefix={<KeyOutlined />}
                      disabled
                    />
                  </Form.Item>
                </Card>

                <Divider />

                <Form.Item>
                  <Space>
                    <Button
                      type="primary"
                      htmlType="submit"
                      icon={<SaveOutlined />}
                      loading={loading}
                      size="large"
                    >
                      Сохранить настройки
                    </Button>
                    <Button
                      onClick={() => form.resetFields()}
                      size="large"
                    >
                      Отменить
                    </Button>
                  </Space>
                </Form.Item>
              </Form>
            </Space>
          </TabPane>

          <TabPane tab="Общие" key="general">
            <Space direction="vertical" size="large" style={{ width: '100%' }}>
              <Card title="Информация о системе">
                <Row gutter={[16, 16]}>
                  <Col span={12}>
                    <Text strong>Версия:</Text>
                    <Paragraph>1.0.0</Paragraph>
                  </Col>
                  <Col span={12}>
                    <Text strong>Тема:</Text>
                    <Paragraph>{theme === 'light' ? 'Светлая' : theme === 'dark' ? 'Темная' : 'Компактная'}</Paragraph>
                  </Col>
                  <Col span={12}>
                    <Text strong>Язык:</Text>
                    <Paragraph>Русский</Paragraph>
                  </Col>
                  <Col span={12}>
                    <Text strong>AI статус:</Text>
                    <Paragraph>
                      {apiKeys?.deepseek ? (
                        <Tag color="success">DeepSeek активен</Tag>
                      ) : (
                        <Tag color="default">AI не настроен</Tag>
                      )}
                    </Paragraph>
                  </Col>
                </Row>
              </Card>

              <Alert
                message="О системе"
                description="CMS - Система управления залоговым имуществом. Версия с поддержкой AI-ассистентов для автоматизации работы с карточками объектов."
                type="info"
              />
            </Space>
          </TabPane>
        </Tabs>
      </Card>
    </div>
  );
};

export default SettingsPage;

