import React from 'react';
import { Card, Row, Col, Tag, Checkbox, Divider, Typography, Space, Badge } from 'antd';
import {
  HomeOutlined,
  CarOutlined,
  ToolOutlined,
  FileTextOutlined,
  SafetyOutlined,
  FileProtectOutlined,
  EyeOutlined,
  CheckCircleOutlined,
  PaperClipOutlined,
} from '@ant-design/icons';
import type { ExtendedCollateralCard } from '@/types';
import './CollateralCardView.css';

const { Title, Text, Paragraph } = Typography;

interface CollateralCardViewProps {
  card: ExtendedCollateralCard;
}

export const CollateralCardView: React.FC<CollateralCardViewProps> = ({ card }) => {
  const getCategoryIcon = () => {
    switch (card.mainCategory) {
      case 'real_estate':
        return <HomeOutlined />;
      case 'movable':
        return <CarOutlined />;
      case 'property_rights':
        return <FileTextOutlined />;
      default:
        return <ToolOutlined />;
    }
  };

  const getCategoryColor = () => {
    switch (card.mainCategory) {
      case 'real_estate':
        return 'blue';
      case 'movable':
        return 'green';
      case 'property_rights':
        return 'orange';
      default:
        return 'default';
    }
  };

  const getStatusColor = () => {
    switch (card.status) {
      case 'approved':
        return 'success';
      case 'editing':
        return 'warning';
      default:
        return 'default';
    }
  };

  const getStatusText = () => {
    switch (card.status) {
      case 'approved':
        return 'Утверждено';
      case 'editing':
        return 'На редактировании';
      default:
        return card.status;
    }
  };

  const renderRealEstateCharacteristics = () => {
    const chars = card.characteristics;
    
    return (
      <>
        <Row gutter={[16, 16]}>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Кадастровый номер:</Text>
              <div className="info-value">{card.address?.cadastralNumber || 'Не указан'}</div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Этажей / Этаж:</Text>
              <div className="info-value">
                {chars.totalFloors || '—'} / {chars.floor || '—'}
              </div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Общая площадь:</Text>
              <div className="info-value">{chars.totalArea ? `${chars.totalArea} м²` : 'Не указана'}</div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Жилая площадь:</Text>
              <div className="info-value">{chars.livingArea ? `${chars.livingArea} м²` : 'Не указана'}</div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Год постройки:</Text>
              <div className="info-value">{chars.buildYear || 'Не указан'}</div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Материал стен:</Text>
              <div className="info-value">{chars.wallMaterial || 'Не указан'}</div>
            </div>
          </Col>
        </Row>

        {chars.balcony !== undefined && (
          <>
            <Divider orientation="left" plain>
              <Text strong>Дополнительно</Text>
            </Divider>
            <Row gutter={[16, 16]}>
              <Col xs={24} sm={12}>
                <Checkbox checked={chars.balcony} disabled>
                  Балкон/Лоджия
                </Checkbox>
              </Col>
              <Col xs={24} sm={12}>
                <div className="info-item">
                  <Text type="secondary" className="info-label">Состояние:</Text>
                  <div className="info-value">{chars.condition || 'Не указано'}</div>
                </div>
              </Col>
            </Row>
          </>
        )}
      </>
    );
  };

  const renderMovableCharacteristics = () => {
    const chars = card.characteristics;
    
    return (
      <Row gutter={[16, 16]}>
        {chars.brand && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Марка:</Text>
              <div className="info-value">{chars.brand}</div>
            </div>
          </Col>
        )}
        {chars.model && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Модель:</Text>
              <div className="info-value">{chars.model}</div>
            </div>
          </Col>
        )}
        {chars.year && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Год выпуска:</Text>
              <div className="info-value">{chars.year}</div>
            </div>
          </Col>
        )}
        {chars.vin && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">VIN:</Text>
              <div className="info-value">{chars.vin}</div>
            </div>
          </Col>
        )}
        {chars.mileage !== undefined && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Пробег:</Text>
              <div className="info-value">{chars.mileage.toLocaleString('ru-RU')} км</div>
            </div>
          </Col>
        )}
        {chars.condition && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Состояние:</Text>
              <div className="info-value">{chars.condition}</div>
            </div>
          </Col>
        )}
        {chars.manufacturer && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Производитель:</Text>
              <div className="info-value">{chars.manufacturer}</div>
            </div>
          </Col>
        )}
        {chars.yearManufactured && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Год производства:</Text>
              <div className="info-value">{chars.yearManufactured}</div>
            </div>
          </Col>
        )}
      </Row>
    );
  };

  const renderIntangibleCharacteristics = () => {
    const chars = card.characteristics;
    
    return (
      <Row gutter={[16, 16]}>
        {chars.registrationNumber && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Регистрационный номер:</Text>
              <div className="info-value">{chars.registrationNumber}</div>
            </div>
          </Col>
        )}
        {chars.registrationDate && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Дата регистрации:</Text>
              <div className="info-value">
                {new Date(chars.registrationDate).toLocaleDateString('ru-RU')}
              </div>
            </div>
          </Col>
        )}
        {chars.expirationDate && (
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Дата истечения:</Text>
              <div className="info-value">
                {new Date(chars.expirationDate).toLocaleDateString('ru-RU')}
              </div>
            </div>
          </Col>
        )}
      </Row>
    );
  };

  const renderCharacteristics = () => {
    switch (card.mainCategory) {
      case 'real_estate':
        return renderRealEstateCharacteristics();
      case 'movable':
        return renderMovableCharacteristics();
      case 'property_rights':
        return renderIntangibleCharacteristics();
      default:
        return <Text type="secondary">Нет данных о характеристиках</Text>;
    }
  };

  return (
    <div className="collateral-card-view">
      {/* Заголовок карточки */}
      <Card className="card-header-section">
        <Row justify="space-between" align="middle">
          <Col>
            <Space size="middle">
              <div className="card-icon" style={{ color: getCategoryColor() }}>
                {getCategoryIcon()}
              </div>
              <div>
                <Title level={4} style={{ margin: 0 }}>
                  {card.number}
                </Title>
                <Text type="secondary">{card.name}</Text>
              </div>
            </Space>
          </Col>
          <Col>
            <Tag color={getStatusColor()} style={{ fontSize: 14, padding: '4px 12px' }}>
              {getStatusText()}
            </Tag>
          </Col>
        </Row>
      </Card>

      {/* Основная информация */}
      <Card title="Основная информация" className="card-section">
        <Row gutter={[16, 16]}>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Тип имущества:</Text>
              <div className="info-value">
                <Tag color={getCategoryColor()} icon={getCategoryIcon()}>
                  {card.classification.level0}
                </Tag>
              </div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Вид объекта:</Text>
              <div className="info-value">{card.classification.level1}</div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Подвид:</Text>
              <div className="info-value">{card.classification.level2}</div>
            </div>
          </Col>
          <Col xs={24} sm={12}>
            <div className="info-item">
              <Text type="secondary" className="info-label">Код ЦБ:</Text>
              <div className="info-value">
                <Badge count={card.cbCode} showZero color="blue" />
              </div>
            </div>
          </Col>
        </Row>

        {card.description && (
          <>
            <Divider plain />
            <div className="info-item">
              <Text type="secondary" className="info-label">Описание:</Text>
              <Paragraph style={{ marginTop: 8 }}>{card.description}</Paragraph>
            </div>
          </>
        )}
      </Card>

      {/* Адрес */}
      {card.address && (
        <Card title="Адрес объекта" className="card-section">
          <div className="info-item">
            <div className="info-value">{card.address.fullAddress}</div>
          </div>
          {card.address.cadastralNumber && (
            <div className="info-item" style={{ marginTop: 12 }}>
              <Text type="secondary" className="info-label">Кадастровый номер:</Text>
              <div className="info-value">{card.address.cadastralNumber}</div>
            </div>
          )}
        </Card>
      )}

      {/* Характеристики */}
      <Card title="Основные характеристики" className="card-section">
        {renderCharacteristics()}
      </Card>

      {/* Партнеры */}
      {card.partners && card.partners.length > 0 && (
        <Card title="Собственники и партнеры" className="card-section">
          <Space direction="vertical" style={{ width: '100%' }}>
            {card.partners.map((partner) => (
              <div key={partner.id} className="partner-item">
                <Space>
                  {partner.type === 'individual' ? (
                    <Text strong>
                      {partner.lastName} {partner.firstName} {partner.middleName}
                    </Text>
                  ) : (
                    <Text strong>{partner.organizationName}</Text>
                  )}
                  <Tag color={partner.role === 'owner' ? 'blue' : 'default'}>
                    {partner.role === 'owner' ? 'Собственник' : 
                     partner.role === 'pledgor' ? 'Залогодатель' : 
                     partner.role === 'appraiser' ? 'Оценщик' : 'Другое'}
                  </Tag>
                  {partner.share && <Text type="secondary">({partner.share}%)</Text>}
                </Space>
                {partner.inn && (
                  <div style={{ marginTop: 4 }}>
                    <Text type="secondary" style={{ fontSize: 12 }}>
                      ИНН: {partner.inn}
                    </Text>
                  </div>
                )}
              </div>
            ))}
          </Space>
        </Card>
      )}

      {/* Статистика документов */}
      <Card title="Наличие документов и данных" className="card-section">
        <Row gutter={[16, 16]}>
          <Col xs={12} sm={8}>
            <Space>
              <SafetyOutlined style={{ fontSize: 18, color: '#1890ff' }} />
              <div>
                <div className="stat-label">Страхование</div>
                <div className="stat-value">
                  {card.insurance && card.insurance.length > 0 ? (
                    <Tag color="success">Есть ({card.insurance.length})</Tag>
                  ) : (
                    <Tag>Нет</Tag>
                  )}
                </div>
              </div>
            </Space>
          </Col>
          <Col xs={12} sm={8}>
            <Space>
              <FileProtectOutlined style={{ fontSize: 18, color: '#52c41a' }} />
              <div>
                <div className="stat-label">Договора</div>
                <div className="stat-value">
                  <Badge count={card.contracts?.length || 0} showZero />
                </div>
              </div>
            </Space>
          </Col>
          <Col xs={12} sm={8}>
            <Space>
              <EyeOutlined style={{ fontSize: 18, color: '#faad14' }} />
              <div>
                <div className="stat-label">Осмотры</div>
                <div className="stat-value">
                  <Badge count={card.inspections?.length || 0} showZero />
                </div>
              </div>
            </Space>
          </Col>
          <Col xs={12} sm={8}>
            <Space>
              <CheckCircleOutlined style={{ fontSize: 18, color: '#722ed1' }} />
              <div>
                <div className="stat-label">Заключения</div>
                <div className="stat-value">
                  <Badge count={card.conclusions?.length || 0} showZero />
                </div>
              </div>
            </Space>
          </Col>
          <Col xs={12} sm={8}>
            <Space>
              <PaperClipOutlined style={{ fontSize: 18, color: '#13c2c2' }} />
              <div>
                <div className="stat-label">Файлы</div>
                <div className="stat-value">
                  <Badge count={card.attachments?.length || 0} showZero />
                </div>
              </div>
            </Space>
          </Col>
        </Row>
      </Card>

      {/* Метаданные */}
      <Card size="small" className="card-metadata">
        <Row gutter={[16, 8]}>
          <Col xs={24} sm={12}>
            <Text type="secondary" style={{ fontSize: 12 }}>
              Создано: {new Date(card.createdAt).toLocaleString('ru-RU')}
            </Text>
          </Col>
          <Col xs={24} sm={12}>
            <Text type="secondary" style={{ fontSize: 12 }}>
              Изменено: {new Date(card.updatedAt).toLocaleString('ru-RU')}
            </Text>
          </Col>
        </Row>
      </Card>
    </div>
  );
};

export default CollateralCardView;

