import React, { useState, useMemo } from 'react';
import { Table, Button, Space, Tooltip, Input, Tag, Modal } from 'antd';
import { EditOutlined, EyeOutlined, DeleteOutlined, SearchOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import type { ColumnsType } from 'antd/es/table';
import './RegistryTable.css';

export interface RegistryTableRecord {
  id: string;
  number: string;
  name: string;
  mainCategory: 'real_estate' | 'movable' | 'intangible';
  status: 'editing' | 'approved' | 'archived';
  classification: {
    level0: string;
    level1: string;
    level2: string;
  };
  addresses?: Array<{
    fullAddress?: string;
  }>;
  createdAt: string | Date;
  updatedAt: string | Date;
}

interface RegistryTableProps {
  data: RegistryTableRecord[];
  loading?: boolean;
  onEdit: (id: string) => void;
  onView: (id: string) => void;
  onDelete: (id: string) => void;
  onDoubleClick: (record: RegistryTableRecord) => void;
}

const { confirm } = Modal;

export const RegistryTable: React.FC<RegistryTableProps> = ({
  data,
  loading,
  onEdit,
  onView,
  onDelete,
  onDoubleClick
}) => {
  const [searchText, setSearchText] = useState('');
  const [pageSize, setPageSize] = useState(30);

  // Фильтрация данных
  const filteredData = useMemo(() => {
    if (!searchText) return data;
    
    const lowerSearch = searchText.toLowerCase();
    return data.filter(item =>
      item.name?.toLowerCase().includes(lowerSearch) ||
      item.number?.toLowerCase().includes(lowerSearch) ||
      item.classification?.level0?.toLowerCase().includes(lowerSearch) ||
      item.classification?.level1?.toLowerCase().includes(lowerSearch) ||
      item.addresses?.some(addr => 
        addr.fullAddress?.toLowerCase().includes(lowerSearch)
      )
    );
  }, [data, searchText]);

  const handleDelete = (id: string, name: string) => {
    confirm({
      title: 'Подтверждение удаления',
      icon: <ExclamationCircleOutlined />,
      content: `Вы действительно хотите удалить карточку "${name}"?`,
      okText: 'Удалить',
      okType: 'danger',
      cancelText: 'Отмена',
      onOk() {
        onDelete(id);
      },
    });
  };

  const columns: ColumnsType<RegistryTableRecord> = [
    {
      title: '№',
      dataIndex: 'number',
      key: 'number',
      width: 120,
      fixed: 'left',
      sorter: (a, b) => a.number.localeCompare(b.number),
      render: (text: string) => (
        <span style={{ fontWeight: 500 }}>{text}</span>
      ),
    },
    {
      title: 'Наименование',
      dataIndex: 'name',
      key: 'name',
      width: 250,
      sorter: (a, b) => a.name.localeCompare(b.name),
      render: (name: string, record) => (
        <div>
          <div style={{ fontWeight: 500 }}>{name}</div>
          <div style={{ fontSize: 12, opacity: 0.7 }}>
            {record.classification?.level1 || '—'}
          </div>
        </div>
      ),
    },
    {
      title: 'Категория',
      dataIndex: 'mainCategory',
      key: 'mainCategory',
      width: 150,
      filters: [
        { text: 'Недвижимость', value: 'real_estate' },
        { text: 'Движимое', value: 'movable' },
        { text: 'НМА', value: 'intangible' },
      ],
      onFilter: (value, record) => record.mainCategory === value,
      render: (category: string) => {
        const categoryConfig = {
          real_estate: { color: 'blue', text: 'Недвижимость' },
          movable: { color: 'green', text: 'Движимое' },
          intangible: { color: 'orange', text: 'НМА' },
        };
        const config = categoryConfig[category as keyof typeof categoryConfig] || { color: 'default', text: category };
        return <Tag color={config.color}>{config.text}</Tag>;
      },
    },
    {
      title: 'Тип объекта',
      dataIndex: ['classification', 'level0'],
      key: 'type',
      width: 200,
      ellipsis: true,
    },
    {
      title: 'Статус',
      dataIndex: 'status',
      key: 'status',
      width: 140,
      filters: [
        { text: 'Редактирование', value: 'editing' },
        { text: 'Согласовано', value: 'approved' },
        { text: 'Архив', value: 'archived' },
      ],
      onFilter: (value, record) => record.status === value,
      render: (status: string) => {
        const statusConfig = {
          editing: { color: 'orange', text: 'Редактирование' },
          approved: { color: 'green', text: 'Согласовано' },
          archived: { color: 'default', text: 'Архив' },
        };
        const config = statusConfig[status as keyof typeof statusConfig] || { color: 'default', text: status };
        return <Tag color={config.color}>{config.text}</Tag>;
      },
    },
    {
      title: 'Адрес',
      dataIndex: 'addresses',
      key: 'address',
      width: 300,
      ellipsis: true,
      render: (addresses: any[]) => (
        <span>{addresses?.[0]?.fullAddress || 'Не указан'}</span>
      ),
    },
    {
      title: 'Дата создания',
      dataIndex: 'createdAt',
      key: 'createdAt',
      width: 130,
      sorter: (a, b) => {
        const dateA = new Date(a.createdAt).getTime();
        const dateB = new Date(b.createdAt).getTime();
        return dateA - dateB;
      },
      render: (date: string | Date) => {
        try {
          return new Date(date).toLocaleDateString('ru-RU');
        } catch {
          return '—';
        }
      },
    },
    {
      title: 'Действия',
      key: 'actions',
      width: 140,
      fixed: 'right',
      render: (_, record) => (
        <Space size="small">
          <Tooltip title="Просмотр">
            <Button 
              type="text" 
              icon={<EyeOutlined />} 
              size="small"
              onClick={(e) => {
                e.stopPropagation();
                onView(record.id);
              }}
            />
          </Tooltip>
          <Tooltip title="Редактировать">
            <Button 
              type="text" 
              icon={<EditOutlined />} 
              size="small"
              onClick={(e) => {
                e.stopPropagation();
                onEdit(record.id);
              }}
            />
          </Tooltip>
          <Tooltip title="Удалить">
            <Button 
              type="text" 
              icon={<DeleteOutlined />} 
              size="small"
              danger
              onClick={(e) => {
                e.stopPropagation();
                handleDelete(record.id, record.name);
              }}
            />
          </Tooltip>
        </Space>
      ),
    },
  ];

  return (
    <div className="registry-table-container">
      <div className="table-header">
        <Input
          placeholder="Поиск по названию, номеру, типу или адресу..."
          prefix={<SearchOutlined />}
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
          style={{ width: 400, maxWidth: '100%' }}
          allowClear
        />
        <Space>
          <span style={{ fontSize: 14 }}>
            Найдено: <strong>{filteredData.length}</strong> из <strong>{data.length}</strong>
          </span>
        </Space>
      </div>

      <Table
        columns={columns}
        dataSource={filteredData}
        loading={loading}
        rowKey="id"
        scroll={{ x: 1400, y: 600 }}
        pagination={{
          pageSize: pageSize,
          showSizeChanger: true,
          showQuickJumper: true,
          showTotal: (total, range) => 
            `Показано ${range[0]}-${range[1]} из ${total} записей`,
          pageSizeOptions: ['15', '30', '50', '100'],
          onShowSizeChange: (_, size) => setPageSize(size),
        }}
        onRow={(record) => ({
          onDoubleClick: () => onDoubleClick(record),
          style: { cursor: 'pointer' },
          className: 'registry-table-row'
        })}
        size="middle"
        className="registry-table"
      />
    </div>
  );
};

export default RegistryTable;

