import React, { createContext, useContext, useState, useEffect } from 'react';
import { ConfigProvider, theme as antdTheme } from 'antd';
import ruRU from 'antd/locale/ru_RU';
import { darkGrayTheme, windowsXPTheme } from '../styles/theme';

type ThemeMode = 'light' | 'dark' | 'windows-xp' | 'dark-gray';

interface ThemeContextType {
  currentTheme: ThemeMode;
  setTheme: (theme: ThemeMode) => void;
}

const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

export const ThemeProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [currentTheme, setCurrentTheme] = useState<ThemeMode>(() => {
    const saved = localStorage.getItem('cms-theme');
    return (saved as ThemeMode) || 'dark-gray';
  });

  useEffect(() => {
    localStorage.setItem('cms-theme', currentTheme);
    // Применяем тему к корневому элементу
    document.documentElement.setAttribute('data-theme', currentTheme);
  }, [currentTheme]);

  const getThemeConfig = () => {
    switch (currentTheme) {
      case 'dark-gray':
        return darkGrayTheme;
      case 'windows-xp':
        return windowsXPTheme;
      case 'dark':
        return {
          algorithm: antdTheme.darkAlgorithm,
          token: {
            colorPrimary: '#1890ff',
            borderRadius: 6,
          },
        };
      case 'light':
      default:
        return {
          algorithm: antdTheme.defaultAlgorithm,
          token: {
            colorPrimary: '#1890ff',
            borderRadius: 6,
          },
        };
    }
  };

  return (
    <ThemeContext.Provider value={{ currentTheme, setTheme: setCurrentTheme }}>
      <ConfigProvider locale={ruRU} theme={getThemeConfig()}>
        {children}
      </ConfigProvider>
    </ThemeContext.Provider>
  );
};

export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useTheme must be used within ThemeProvider');
  }
  return context;
};

